        <p>Better understand your villa life</p>
        <p>Villagers</p>
        <p>Let you experience "real villa life"</p>
        <p>More intimate service</p>
      </div>
    </div>
    <p class="text">
      <span></span>201508
    </p>
    <!--  -->
    <div class="support text_cen">
      <h5></h5>
      <p></p>
      <div class="flex">
        <div class="item" v-for="(item,index) in teamList" :key="index" @click="handdtail(item.id)">
          <img :src="item.cover" alt />
          <div class="block">
            <span>{{item.name}}</span>
            <span>{{item.position}}</span>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="black text_cen">
        <img src="../assets/img/sh2.png" alt />
        <div class="block" style="background: rgba(255,255,255,.1);">
          <p></p>
          <p>1020000+</p>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="business text_cen">
      <h5></h5>
      <p></p>
      <div class="dpic flex_be">
        <img :src="link+item.cover" alt v-for="(item,k) in licenselist" :key="k" />
      </div>
    </div>
    <!--  -->
    <div class="show text_cen">
      <h5></h5>
      <p></p>
      <div>
        <div class="dpic1 flex_be">
          <img :src="link+item.cover" alt v-for="(item,k) in dzlist" :key="k" />
        </div>
        <div class="xpic flex_be">
          <img :src="item.cover" alt v-for="(item,k) in moreList" :key="k" />
        </div>
      </div>
      
      <div  v-if="moreList.length<=picList.length"></div>
      <div class="more" @click="more" v-else></div>
    </div>
    <!--  -->
    <div class="mouth text_cen">
      <h5></h5>
      <p></p>
      <div class="flex_be">
        <img :src="link+item.cover" alt v-for="(item,k) in dzlist" :key="k" />
      </div>
      <div class="more">
        <a :href="'tel:' + phone"></a>
      </div>
    </div>
    <tabbar :tabid="2" />
  </div>
</template>

<script>
import tabbar from "@/components/tabBar.vue";
import request from "@/request.js";
export default {
  components: {
    tabbar,
  },
  data() {
    return {
      phone: 18883614674,
      teamList: [], //
      licenselist: [],
      link: "http://villa.jisapp.cn",
      dzlist: [],
      picList: [],
      moreList: [],
    };
  },
  created() {
    request
      .getTeam({
        page: 1,
      })
      .then((res) => {
        console.log(res, "");
        this.teamList = res.data;
      })
      .catch(() => {})
      .finally(() => {});
    this.getdata();
    this.getTail();
  },
  methods: {
    more() {
      let list = [...this.moreList]
      this.moreList = [...list, ...this.picList.slice(this.idx++, this.idx + 1)]
    },
    getTail() {
      // 
      request
        .teamDrawings({
          page: 1,
        })
        .then((res) => {
          console.log(res, "");
          this.picList = res.data;
          if (this.picList < 4) {
            this.moreList = this.picList;
          } else {
            this.moreList = this.picList.slice(0, 4);
          }
        })
        .catch(() => {})
        .finally(() => {});
    },
    getdata() {
      this.flag = true;
      request
        .getHomeData()
        .then((res) => {
          console.log(res, "");
        })
        .catch(() => {})
        .finally(() => {});
      request
        .getDrawings({})
        .then((res) => {
          console.log(res, "");
          this.licenselist = res.data.license;
          this.dzlist = res.data.praise;
        })
        .catch(() => {})
        .finally(() => {});
    },
    handdtail(num) {
      this.$router.push({
        path: "/desigerDetail",
        query: {
          id: num,
        },
      });
    },
  },
};
</script>

