<template>
  <!--  -->
  <div class="register">
    <div class="main">
      <h6></h6>
      <p></p>
      <van-form @submit="onSubmit">
        <div class="tel">
          <van-field
            v-model="tel"
            name="pattern"
            clearable
            placeholder=""
            :rules="[{ pattern, message: '' }]"
          />
        </div>

        <div class="code flex_be">
          <van-field
            v-model="code"
            name="pattern"
            clearable
            placeholder=""
            :rules="[{ pattern:pattern1, message: '' }]"
          />
          <span v-if="tmeValue==60" @click="getauth"></span>
          <span v-else>{{ tmeValue }} s </span>
        </div>

        <div>
          <van-button block class="button" native-type="submit"></van-button>
        </div>
      </van-form>
      <p class="agreement text_cen">/</p>
      <div class="text_cen">
        <van-icon name="cross" />
      </div>
    </div>
  </div>
</template>

<script>
// import { mapState } from "vuex";
import request from "@/request.js";
export default {
  data() {
    return {
      tel: "",
      code: "",
      pattern: /\d{11}/,
      pattern1: /\d{4}/,
      tmeValue: 60, //
      flag: 0,
    };
  },
  methods: {
    getauth() {
      let that = this
      if (/^1[34578]\d{9}$/.test(that.tel)) {
        request
          .getCode({
            phone_num: that.tel,
          })
          .then((res) => {
            // if (res.data.code == 0) {
              console.log(res, "");
              this.$toast("");
              that.time()
            // } else {
            //   this.$toast("");
            // }
          })
          .catch(() => {
            this.$toast("");
          })
          .finally(() => {});
      } else {
        this.$toast("");
      }
    },
    time() {
      //
      this.tmeValue = this.tmeValue - 1;
      this.flag = 1;
      if (this.tmeValue <= 0) {
        this.tmeValue = 60;
        this.flag = 0;
        return "";
      } else {
        setTimeout(() => {
          this.time();
        }, 1000);
      }
    },
    onSubmit(values) {
      request
        .loginByMsg({
          phone_num: this.tel,
          msg_code: this.code,
        })
        .then((res) => {
          // if (res.data.code == 0) {
            console.log(res, "");
            this.$store.commit('settoken', res.data)
            localStorage.setItem('istoken', res.data.token)
            this.$toast("");
            this.$router.push({
              path: "/",
            });
          // } else {
          //   this.$toast("");
          // }
        })
        .catch(() => {
          this.$toast("");
        })
        .finally(() => {});
      console.log("submit", values);
    },
  },
};
</script>

<style lang="less" scoped>
@import "../styles/index.less";
@import "../styles/variable.less";
.register {
  background-color: #eee;
  height: 100%;
  position: fixed;
  width: 100%;
  font-family: SimHei;
  .main {
    background: #fff;
    .margin(217,20,0,20);
  }
  h6 {
    color: #202020;
    text-align: center;
    .fs(32);
    font-weight: bold;
    .lh(77);
  }
  p {
    color: #424242;
    font-family: SimHei;
    .fs(24);
    .lh(58);
    .ml(44);
  }
  .code {
    border-bottom: 1px solid #eee;
    .margin(0, 18);
    span {
      display: flex;
      width: 35%;
      color: #979797;
      .fs(32);
    }
  }
  .van-cell::after {
    border-bottom: 0;
  }
  .van-icon {
    .mb(30);
  }
  .tel {
    .margin(0, 18);
    border-bottom: 1px solid #eee;
  }
  .button {
    background: #fe9e15;
    color: #fff;
    .fs(36);
    .b-radius(10);
    margin: auto;
    .mt(60);
    .w(699);
  }
  .agreement {
    line-height: 1;
    .mt(47);
    .mb(82);
  }
}
</style>