<template>
  <!--  -->
  <div class="productdetail">
    <van-swipe @change="onChange">
      <van-swipe-item v-for="(item,k) in ProDetail.imgs" :key="k">
        <img :src="item" alt />
      </van-swipe-item>
      <template #indicator>
        <div class="custom-indicator text_cen">{{ current + 1 }}/ProDetail.imgs.length</div>
      </template>
    </van-swipe>
    <!--  -->
    <div class="price">
      <span>{{ProDetail.price}}</span>
      <span></span>
    </div>
    <!--  -->
    <div class="sharebox">
      <p class="title">{{ProDetail.title}}</p>
      <div class="share flex_cen">
        <img src="../assets/img/share.png" alt />
        <span></span>
      </div>
    </div>

    <!--  -->
    <div class="flex_be goods">
      <div>
        
        <span></span> | 
        <span></span>
      </div>
      <div>{{ProDetail.moods}}</div>
    </div>
    <!--  -->
    <div class="views flex_be">
      <div>
        <img src="../assets/img/1.png" alt />
        <img src="../assets/img/1.png" alt />
      </div>
      <div>{{ProDetail.moods}}</div>
    </div>
    <!--  -->
    <div class="serves flex_be" @click="showPopup">
      <div>
        <span></span>
        <span>48</span>
      </div>
      <van-icon name="arrow" />
    </div>
    <van-popup v-model="show" position="bottom" :style="{ height: '68%' }">
      <div class="pop">
        <div class="basics text_cen"></div>
        <van-checkbox-group v-model="result" ref="checkboxGroup" @change="handchange">
          <van-checkbox name="a" icon-size="0.3rem" checked-color="#000" class="span1">
            <span>48</span>
          </van-checkbox>
          <van-checkbox name="b" icon-size="0.3rem" checked-color="#000" class="span2">
            <span>7</span>
          </van-checkbox>
          <div class="yell">7</div>
          <div class="other text_cen"></div>
          <van-checkbox name="c" icon-size="0.3rem" checked-color="#000" class="span3">
            <span></span>
          </van-checkbox>
          <van-checkbox name="d" icon-size="0.3rem" checked-color="#000" class="span4">
            <span></span>
          </van-checkbox>
        </van-checkbox-group>
      </div>
      <div class="btn text_cen" @click="closepanl"></div>
    </van-popup>
    <!--  -->
    <div class="introduce">
      <span></span>
      <p>{{ProDetail.intro}}</p>
    </div>
    <!--  -->
    <div class="fixed">
      <van-goods-action>
        <van-goods-action-icon icon="shop" text="" color="#676767" class="size" />
        <van-goods-action-icon icon="service" text="" color="#676767" class="size" />
        <van-goods-action-icon
          :icon="icon"
          :text="text"
          color="#676767"
          class="size"
          @click="onClickIcon(ProDetail.id)"
        />
        <van-goods-action-button type="warning" text="" class="size" @click="gocard" />
        <van-goods-action-button text="" class="size danger" @click="gosurepay" />
      </van-goods-action>
    </div>
    <div>
      <van-popup
        v-model="showpop"
        position="bottom"
        :style="{ height: '60%' }"
        style="border-radius:0.3rem 0.3rem 0px 0px;"
      >
        <div class="cartfff">
          <div class="cartbox">
            <div class="cartlist flex">
              <img :src="ProDetail.cover" alt />
              <div class="red">{{ProDetail.price}}</div>
            </div>
            <div class="flex_be stepper">
              <span class="num"></span>
              <div>
                <span :class="[cunt==1?'disabled':'squer']" @click="reducecart(ProDetail)">-</span>
                <span class="squer squer1">{{cunt}}</span>
                <span class="squer" @click="addcart(ProDetail)">+</span>
              </div>
            </div>
          </div>
          <button @click="goJoinCart"></button>
        </div>
      </van-popup>
    </div>
  </div>
</template>

<script>
// import { Toast } from "vant";
import { mapState } from "vuex";
import request from "@/request.js";
export default {
  data() {
    return {
      current: 0,
      show: false,
      result: ["a", "b", "c", "d"],
      text: "",
      icon: "like-o",
      showpop: false,
      ProDetail: [],
      cunt: 1, //
      cartinfoList: [], //
    };
  },
  computed: {
    ...mapState({
      token: (state) => state.token,
      userInfor: (state) => state.userInfor,
    }),
  },
  created() {

    this.handdetail();
  },
  methods: {
    gosurepay() {
      this.$router.push({
        path: "/surePay",
      });
    },
    onChange(index) {
      //
      this.current = index;
    },
    showPopup() {
      //
      this.show = true;
      console.log(this.result);
    },
    closepanl() {
      //
      this.show = false;
    },
    handchange(names) {
      //
      console.log(names);
    },
    gocard() {
      // 
      this.showpop = true;
    },
    handdetail() {
      request
        .getProDetail({
          uid: this.userInfor.member_id,
          id: this.$route.query.id,
        })
        .then((res) => {
          console.log(res, "");
          this.ProDetail = res.data;
        })
        .catch(() => {
          // this.$toast("");
        })
        .finally(() => {});
    },
    onClickIcon(num) {
      request
        .getCollect({
          uid: this.userInfor.member_id,
          type: 1,
          object: num,
        })
        .then((res) => {
          console.log(res);
          this.handdetail();
          this.$toast("");
          (this.text = ""), (this.icon = "like");
        })
        .catch((e) => {
          console.log(e);
          this.$toast("");
        })
        .finally(() => {});
    },
    // 
    goJoinCart() {
      //
      request
        .getJoinCart({
          uid: this.userInfor.member_id,
          b_id: 2,
          num: this.cunt,
        })
        .then((res) => {
          console.log(res, "");
          this.$toast({
            message: "",
            icon: "success",
          });
        })
        .catch(() => {
          this.$toast("");
        })
        .finally(() => {});
    },
    reducecart(item) {
      console.log(item);
      if (this.cunt == 1) {
        return false;
        // request
        //   .getReduceCart({
        //     uid: this.userInfor.member_id,
        //     id: item.b_id,
        //   })
        //   .then((res) => {
        //     console.log(res, "");
        //     this.$toast("");
        //     this.CartInfo();
        //   })
        //   .catch(() => {
        //     this.$toast("");
        //   })
        //   .finally(() => {});
      }
      this.cunt--;
    },
    addcart(item) {
      console.log(item);
      this.cunt++;
    },
  },
};
</script>

