<script>
// import { mapState } from "vuex";
import request from "@/request.js";
import $ from "jquery";
export default {
  data() {
    return {
      username: "",
      tel: "",
      area: "",
      message: "",
    };
  },
  created() {},
  methods: {
   
    onSubmit(values) {
      if ($(".input").is(":checked")) {
       console.log( $(".input").parent().text())
        request
          .getTeamwork({
            name: this.username,
            phone: this.tel,
            address: this.area,
            message: this.message,
          })
          .then((res) => {
            console.log(res, "");
            this.$toast("");
          })
          .catch(() => {
            this.$toast("");
          })
          .finally(() => {});
      }else{
         console.log( $(".input1").parent().text())
        request
          .getTeamwork({
            name: this.username,
            phone: this.tel,
            address: this.area,
            message: this.message,
          })
          .then((res) => {
            console.log(res, "");
            this.$toast("");
          })
          .catch(() => {
            this.$toast("");
          })
          .finally(() => {});
      }
      console.log("submit", values);
    },
    go(){
      this.$router.go(-1);
    }
  },
};
</script>

