<template>
  <!-- -->
  <div class="orderlist">
    <main v-if="orderlist.length!==0">
      <div @click="godetail(navid)" v-for="(item,k) in orderlist" :key='k'>
        <div class="tagname flex_be">
          <span>2020-07-16 13:49:52</span>
          <span v-if="navactivechoseid===0"></span>
          <span v-else-if="navactivechoseid===1"></span>
          <span v-else-if="navactivechoseid===2"></span>
        </div>
        <div class="flex">
          <img :src="item.img" alt />
          <div class="right">
            <p>{{item.p}}</p>
            <div class="text_r">
              <van-icon name="cross" />{{item.num}}
            </div>
            <div>{{item.price}}</div>
          </div>
        </div>
        <div class="text_r price_pay" v-if="navactivechoseid===0">1 :598.00</div>
        <div class="text_r price_pay" v-else-if="navactivechoseid===1">1 :598.00</div>
        <div class="text_r price_pay" v-else-if="navactivechoseid===2">1 :598.00</div>
      </div>

      <div class="bor_red" v-if="navactivechoseid===0">
        <span class="text_cen"></span>
        <span class="text_cen" @click="gopay"></span>
      </div>
      <div class="bor_red" v-else-if="navactivechoseid===1"></div>
      <div class="bor_red" v-else-if="navactivechoseid===2">
        <span class="text_cen" @click="goLogistics"></span>
      </div>
    </main>
    <main v-else class="nodata"></main>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: mapState({
    navactivechoseid: state => state.navactivechoseid
  }),
  props: {
    orderlist: {
      type: Array,
      default: () => []
    },
    navid: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {};
  },
  created() {
    console.log(this.orderlist);
  },
  methods: {
    godetail(navid) {
      //
      if (navid === 0) {
        this.$router.push({
          path: "/orderpay"
        });
      } else if (navid === 1) {
        this.$router.push({
          path: "/stayGoods"
        });
      } else if (navid === 2) {
        this.$router.push({
          path: "/productDetail"
        });
      }
    },
    goLogistics() { //
      this.$router.push({
        path: "/logisticsInfo"
      });
    },
    gopay(){ //
      this.$router.push({
        path: "/logisticsInfo"
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import "../styles/index.less";
@import "../styles/variable.less";

main {
  background: #fff;
  .b-radius(10);
  .margin(0, 20);
  .padding(0, 20, 36, 36);
  .tagname {
    .lh(83);
    border-bottom: 1px solid #f3f3f3;
    .mb(34);
  }
  img {
    .w(156);
    .h(156);
    .b-radius(10);
    .mr(9);
  }
  p {
    color: #2a2a2a;
    .fs(26);
    .lh(37);
  }
  .right {
    width: 80%;
  }
  .text_r {
    text-align: right;
    .pb(23);
  }
  .price_pay {
    .mt(60);
    border-bottom: 1px solid #f3f3f3;
  }
  .bor_red {
    text-align: right;
    .mt(24);
    span:nth-of-type(1) {
      .lh(54);
      .w(150);
      border: 1px solid #797979;
      display: inline-block;
      .b-radius(27);
    }
    span:nth-of-type(2) {
      .lh(54);
      .w(150);
      border: 1px solid #fd3723;
      display: inline-block;
      .b-radius(27);
      .ml(20);
    }
    span:nth-last-of-type(1) {
      .lh(54);
      .w(150);
      border: 1px solid #fd3723;
      display: inline-block;
      .b-radius(27);
    }
  }
}
.nodata {
  color: #afafaf;
  .fs(30);
  background: #eee;
}
</style>