<template>
  <!--  -->
  <div class="newshippingAddr">
    <div class="flex navigation">
      <van-icon name="cross" @click="go"/>
      <span></span>
    </div>
    <!--  -->
    <div style="background:#fff">
      <div class="nav_bar flex">
        <van-icon name="arrow-left" @click="go" />
        <span></span>
      </div>
    </div>

    <!--  -->
    <div class="p">
      <p>  18112345678 3</p>
    </div>
    <!--  -->
    <div style="background:#fff">
      <div class="cotbtn flex">
        <span></span>
        <span></span>
      </div>
    </div>

    <!--  -->
    <div class="cotaddr">
      <van-field v-model="name" label="" clearable placeholder="" />
      <div class="mainland">
        <span>+86</span>
        <span></span>
      </div>
      <van-field v-model="tel" type="tel" label="" clearable placeholder="" />
      <div class="choseaddr">
        <van-field
          readonly
          clickable
          name="area"
          :value="value"
          label=""
          placeholder="----"
          @click="showArea = true"
        />
        <van-popup v-model="showArea" position="bottom">
          <van-area :area-list="areaList" @confirm="onConfirm" @cancel="showArea = false" />
        </van-popup>
      </div>
      <van-divider :style="{ color: '#1989fa', borderColor: '#eee', padding: '0' }"></van-divider>
      <van-field v-model="addr" type="tel" label="" clearable placeholder="" />
      <div class="flex_be swich">
        <span></span>
        <van-switch v-model="checked" active-color="#07c160" inactive-color="#EEEEEE" />
      </div>
    </div>
    <div class="eee" ref="eee">
      <button @click="onSave"></button>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import request from "@/request.js";
import areaList from "../assets/city";
export default {
  data() {
    return {
      name: "",
      tel: "",
      addr: "",
      checked: false,
      value: "",
      showArea: false,
      areaList: areaList //  Area 
    };
  },
  computed: {
    ...mapState({
      token: (state) => state.token,
      userInfor: (state) => state.userInfor,
      headimg: (state) => state.headimg,
    }),
  },
  methods: {
    // 
    onSave(){
      let add=this.value.split('/');
      console.log(add)
      request.getAddr({
          uid: this.userInfor.member_id,
          address:this.addr,
          name:this.name,
          phone:this.tel,
          province:add[0],
          city:add[1],
          district:add[2],
          is_default:this.checked
      }).then(res=>{
        console.log(res,'')
        this.$toast("");
      }).catch(() => {
          this.$toast("");
        })
        .finally(() => {})
      if(this.name==''){
        this.$toast('')
        return false;
      }
      if(this.tel==''){
        this.$toast('')
        return false;
      }
      if(this.value==''){
        this.$toast('')
        return false;
      }
      if(this.addr==''){
        this.$toast('')
        return false;
      }
    },
    // 
    onConfirm(values) {
      this.value = values.map(item => item.name).join("/");
      this.showArea = false;
    },
    go(){
      this.$router.go(-1)
    }
  },
  mounted() {}
};
</script>

<style lang="less" scoped>
@import "../styles/index.less";
@import "../styles/variable.less";
