  background: #fff;
  border-bottom: 1px solid #d6d6d6;
  .h(79);
  align-items: center;
  .mr(21);
  .ml(21);
  span {
    color: #070707;
    .fs(30);
    font-weight: bold;
    .lh(34);
    .ml(235);
    font-family: SimHei;
  }
