}
.cotbtn {
  background: #fff;
  border-bottom: 0.19rem solid #eeeeee;
  .pb(22);
  .mr(21);
  .ml(21);
  .pt(22);
  align-items: center;
  justify-content: flex-end;
  span {
    display: block;
  }
  span:nth-of-type(2) {
    .w(152);
    .h(56);
    .b-radius(10);
    border: 1px solid #ff0000;
    color: #ff0000;
    .fs(30);
    text-align: center;
    box-sizing: border-box;
    .lh(56);
    .ml(46);
